import React from "react";
import classNames from "classnames";
import * as style from "../../assets/scss/index.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {graphql, useStaticQuery} from "gatsby";
import Img from "gatsby-image";


export const Certification: React.FunctionComponent<any> = React.memo((props: any) => {
    const {
        csm,
        csd,
        cspo,
        mvp
    } = props
    return <div className={classNames(style.row)}>
        <div className={classNames(style.col)}>
            <Img
                className={classNames(style.imgFluid, style.w100)}
                fluid={csm.fluid}
                fadeIn={true}
                alt="CSM"/>
        </div>
        <div className={classNames(style.col, style.formInline)}>
            <Img
                className={classNames(style.imgFluid, style.w100)}
                fluid={csd.fluid}
                fadeIn={true}
                alt="CSD"/>
        </div>
        <div className={classNames(style.col)}>
            <Img
                className={classNames(style.imgFluid, style.w100)}
                fluid={cspo.fluid}
                fadeIn={true}
                alt="CSPO"/>
        </div>
        <div className={classNames(style.col, style.formInline)}>
            <Img
                className={classNames(style.imgFluid, style.w100)}
                fluid={mvp.fluid}
                fadeIn={true}
                alt="MVP"/>
        </div>
    </div>
});
